body {
    padding: 0;
    font-family: 'Poppins', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font: 16px "Poppins", sans-serif;

    margin: 0px;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}
.game-container {
    height: 100vh;
}

.game-screen {
    padding: 35px 35px 50px;
    background: -webkit-gradient(linear, left top, left bottom, from(#264653), color-stop(#2A9D8F), to(#E9C46A));
    background: -webkit-linear-gradient(top, #264653, #2A9D8F, #E9C46A);
    background: linear-gradient(180deg, #264653, #2A9D8F, #E9C46A);
    color: white;
    box-shadow: 0 2px 14px 0 rgba(0, 0, 0, .6);
}

.game__board {
    display: flex;
    justify-content: center;
}

.game__status {
    margin-bottom: 20px;
    text-align: center;
}


/*-------------------------------------------Square style------------------------------------------------*/

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-weight: bold;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
}

.square:focus {
    outline: none;
}

.square--highlight {
    background: #030303;
    color: white;
    -webkit-animation: zoomHighlight 0.9s ease-in-out infinite alternate;
            animation: zoomHighlight 0.9s ease-in-out infinite alternate;
}

.square--highlight:focus,
.square--highlight:active {
    background: #030303;
    color: white;

}

.current--move {
    background: #999;
}

.square[data-pro="X"] {
    color: #E76F51;
}

.square[data-pro="O"] {
    color: #264653;
}

/*-------------------------------------------game__history-section style------------------------------------------------*/

.game__history-section {
    margin-top: 40px;
    padding: 10px;
    display: flex;
    background: #08092650;
    width: 100%;
    max-width: 150px;
    flex-direction: column;
}


.sorting-btn-wrapper {
    text-align: right;
    max-width: 150px;
    margin-bottom: 10px;
}

.sorting-btn__text {
    margin-right: 10px;
}

.sorting-btn__icon {
    width: 24px;
}


.history-btn {
    color: white;
    border: 1px solid rgb(186, 186, 186);
    background: transparent;
    display: block;

}

.history-btn:not(:last-child) {
    margin-bottom: 10px
}

.history-btn:focus,
.history-btn:active {
    outline: none;
}

.history-btn--selected {
    font-weight: bold;
    border: 3px solid rgb(255, 255, 255);
}


.cursor--pointer {
    cursor: pointer;
}

@-webkit-keyframes zoomHighlight {
    0% {
        -webkit-transform: scale(0.85);
                transform: scale(0.85)
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1)
    }
}

@keyframes zoomHighlight {
    0% {
        -webkit-transform: scale(0.85);
                transform: scale(0.85)
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1)
    }
}
